import React from 'react'
import "./Home.css"
import Typewriter from 'typewriter-effect';
// import MyResume from "../Navbar/Myresume.pdf"

function HomeSection() {
    const downloadHandle = () => {
        const link = document.createElement("a");
        link.download = "Surendra-Singh-resume.pdf";
        // link.href = MyResume;
        link.setAttribute("class", "resume-link-2")
        link.click();

        window.open(
            "https://drive.google.com/file/d/1knJQQqP8Dj_8nnpbsmol6fHznDd5Qfz1/view?usp=sharing",
            "_blank"
        );
    };

    const githubHandle = () => {
        window.open(
            "https://github.com/SursaChauhan",
            "_blank"
        );
    };
    return (
        <div id='home'>
            <div id='left_home'>
                <div>
                    <h1>Hi, I am</h1>
                    <h1 id="user-detail-name">Surendra Singh</h1>
                    
                    <h3>
                        I am a
                        <span>
                            <Typewriter
                                options={{
                                    strings: ["Full Stack Web Developer", "Passionate Coder", "Tech Enthusiast"],
                                    autoStart: true,
                                    loop: true,
                                }}
                            />
                        </span>
                    </h3>
                    <p> As a versatile and creative programmer, I bring enthusiasm, a positive attitude, and a growth mindset to every project. Eager to deliver exceptional results and make a meaningful contribution, I thrive in the ever-evolving world of programming. Let's connect and explore possibilities! Reach out to me a <a href="#contact" className='contactMe'>Contact Me</a></p>
                    <div className='home_button'>
                        <button id="resume-button-2"
                            onClick={downloadHandle}>
                            Resume
                        </button>
                        <button onClick={githubHandle} >
                            GitHub
                        </button>
                    </div>
                </div>
            </div>
            <div id='right_home' >
                <img src="https://github.com/SursaChauhan/SursaChauhan.github.io/assets/132122228/5535d768-0dfe-4122-800e-714cae577491" alt="" className="home-img" />
            </div>
        </div>
    )
}

export default HomeSection