import React from 'react'
import "./Contact.css"
import Phone from './contactImages/phone.png'
import Email from './contactImages/Email.jpg'
import LinkedIn from './contactImages/linkedin.png'
import Github from './contactImages/github.png'

function Contact() {

    const sendto = (link) => {
        window.open(
            link,
            "_blank"
        );
    }

    return (
        <div id='contact'>
            <h1>Contact Me</h1>
            <div>
                <div id="contact-github" className='contactCard' onClick={() => { sendto("https://github.com/SursaChauhan") }}>
                    <img src={Github} alt="" />
                    <h5>Github</h5>
                    <p>SursaChauhan</p>
                </div>
                <div id="contact-linkedin" className='contactCard' onClick={() => { sendto("https://www.linkedin.com/in/surendra-schauhan/") }}>
               
                    <img src={LinkedIn} alt="LinkedIn" />
                    <h5>Linkedin</h5>
                    <p>surendra-schauhan</p>
                  
                </div>
                <div id="contact-email" className='contactCard'>
                <a href="mailto:ss0622187@gmail.com">
                    <img src={Email} alt="" />
                    </a>
                    <h5>Email</h5>
                    <p>ss0622187@gmail.com</p>
                </div>
                <div id="contact-phone" className='contactCard'>
                <a href="tel:+917014007650">
                    <img src={Phone} alt="" />
                    </a>
                    <h5>Phone</h5>
                    <p>+91 7014007650</p>
                </div>
            </div>
        </div>
    )
}

export default Contact