import React from 'react'
import AutoPart from './projectImages/AutoPart.png'
import BedBath from './projectImages/BedBath.png'
import Myntra from './projectImages/Myntra.png'
import "./Project.css"
// import { motion } from "framer-motion"

const data = [

    {
        img: AutoPart,
        projectTitle: "AutoPart Shop",
        techStack: ["HTML", "JavaScript", "CSS", "firebase"],
        description: "Autoparts is an online e-commerce website where you will find wide varity of autoparts products for different automobiles. Many functionalities are provided in the web application like login/signup, cart and filtering the products with its category along with payment and full responsiveness.",
        githubLink: "https://github.com/SursaChauhan/Online-Auto-Part-shop",
        appLink: "https://sursachauhan.github.io/Online-Auto-Part-shop/"
    },
    {
        img: BedBath,
        projectTitle: "Bed Bath & Beyond",
        techStack: ["HTML", "JavaScript", "CSS"],
        description: "Bed Bath & Beyond is a popular retail chain in the United States that specializes in home goods and furnishings. The Bed Bath & Beyond website serves as an online platform where customers can browse and purchase a wide range of products for their homes.",
        githubLink: "https://github.com/SursaChauhan/Bed-Bath-And-Beyond-Clone",
        appLink: "https://effortless-raindrop-0b52c3.netlify.app/"
    },
    {
        img: Myntra,
        projectTitle: "Myntra",
        techStack: ["HTML", "JavaScript", "CSS"],
        description: "Myntra, a leading Indian online retailer, offers a diverse selection of fashion and lifestyle products. Explore accessories and lifestyle items conveniently on the Myntra website for a seamless online shopping experience, providing quality products for wardrobe and lifestyle needs.",
        githubLink: "https://github.com/SursaChauhan/Myntra-Clone",
        appLink: "https://gilded-vacherin-2e78d2.netlify.app/"
    },

]




function Projects() {
    return (
        <div id='projects'>
            <h1>Projects</h1>

            {
                data.map((e, index) => {
                    return (

                        index % 2 == 0 ? (
                            <div className="project-card proCard1" key={index}>
                                <div className='projectImg'>
                                    <img src={e.img} alt="" />
                                </div>
                                <div className='projectDetails'>
                                    <h2 className="project-title">{index + 1 + ". " + e.projectTitle}</h2>
                                    <div className="project-tech-stack">
                                        {
                                            e.techStack.map((ele, ind) => {
                                                return (
                                                    <span key={ind}>{ele}</span>
                                                )
                                            })
                                        }
                                    </div>
                                    <p className="project-description">{e.description}</p>
                                    <div className='projectButtons'>
                                        <a href={e.githubLink} target="_blank" className="project-github-link">View Code</a>
                                        <a href={e.appLink} target="_blank" className="project-deployed-link">View Live App</a>
                                    </div>
                                </div>
                            </div>
                        ) : (
                            <div className="project-card proCard2" key={index}>
                                <div className='projectDetails'>
                                    <h2 className="project-title">{index + 1 + ". " + e.projectTitle}</h2>
                                    <div className="project-tech-stack">
                                        {
                                            e.techStack.map((ele, ind) => {
                                                return (
                                                    <span key={ind}>{ele}</span>
                                                )
                                            })
                                        }
                                    </div>
                                    <p className="project-description">{e.description}</p>
                                    <div className='projectButtons'>
                                        <a href={e.githubLink} target="_blank" className="project-github-link">View Code</a>
                                        <a href={e.appLink} target="_blank" className="project-deployed-link">View Live App</a>
                                    </div>
                                </div>
                                <div className='projectImg'>
                                    <img src={e.img} alt="" />
                                </div>
                            </div>
                        )
                    )
                })
            }
        </div>
    )
}

export default Projects
